
@mixin FontPoppins() {
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	color: #2D2E46;
}

@mixin FontSanFrancisco() {
	font-family: 'SF UI Display', sans-serif;
}

.login {
	width: 413px;
	height: 600px;
	background: #F9F9FE;
	border-radius: 8px;
	display: flex;
	text-align: center;
	flex-direction: column;

	&__logo {
		margin-top: 25px;
		@include FontPoppins();
		font-size: 26px;
		line-height: 39px;
		margin-bottom: 0;
	}

	.form {
		display: flex;
		flex-direction: column;
		margin: 32px 33px 0 33px;

		&__title {
			@include FontPoppins();
			font-size: 22px;
			line-height: 33px;
			margin: 0 0 32px 0;
		}

		&__group {
			display: flex;
			flex-direction: column;
			position: relative;

		}

		&__passwordSettings {
			display: flex;
			justify-content: space-between;
			margin-top: 14px;
			&_forgot {
				cursor: pointer;
				margin-left: 15px;
				font-weight: 500;
				font-size: 16px;
				color: #2D2E46;
				opacity: 0.5;
				text-decoration: underline;
			}
		}

		&__buttonWrap {
			margin-top: 30px;
		}
	}

	&__quest {
		margin-top: 30px;
		@include FontSanFrancisco();
		font-weight: 600;
		font-size: 14px;
		line-height: 24px;
		text-align: center;
		color: #2D2E46;
		opacity: 0.5;
	}

	&__subTitle,
	&__demo {
		margin-top: 11px;
		@include FontSanFrancisco();
		font-weight: 600;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #21268F;
	}

	&__errorMessage {
		margin-top: 20px;
		color: red;
	}
}

